:root {
  --font-main: "Mont", Arial, Helvetica, sans-serif;
  --font-second: "Roboto", Arial, Helvetica, sans-serif;
  --font-size: 16px;
  --font-size-var1: clamp(18px, 1.85vw, 24px);
  --font-size-var2: clamp(12px, 1.23vw, 16px);
  --color-bg: #ffffff;
  --color-white: #ffffff;
  --color-dark: #484848;
  --color-green: #12b992;
  --color-orange: #f8c227;
  --color-blue: #225980;
  --color-gray: #413e5e;
}
a {
  text-decoration: none !important;
  color: inherit;
}

html {
  background-color: var(--color-bg);
  margin-right: 0 !important;
}
body {
  font-family: var(--font-main);
  font-style: normal;
  font-weight: normal;
  font-size: var(--font-size-var2);
  color: var(--color-dark);
}
.wrapper {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100%;
}
img {
  max-width: 100%;
}

.section {
  max-width: 100%;
  width: 100%;
  position: relative;
  &__content {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px;
  }
}
._1 {
  background: #f8c227;

  .section__content {
    padding: 20px 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container {
    width: 100%;
    display: flex;
    padding: 0 20px;
  }
  h3 {
    position: absolute;
    font-size: 24px;
    bottom: 20px;
    left: 20px;
    margin: 0;
    opacity: 0;
  }
  .slide {
    position: relative;
    height: 50vh;
    border-radius: 20px;
    margin: 10px;
    flex: 1;
    color: #ffffff;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: all 1s ease-in-out;
    &.active {
      flex: 10;
      h3 {
        opacity: 1;
        transition: opacity 0.3s ease-in;
      }
    }
    &.first {
      background-image: url("https://images.unsplash.com/photo-1470071459604-3b5ec3a7fe05?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2174&q=80");
    }
    &.second {
      background-image: url("https://images.unsplash.com/photo-1470252649378-9c29740c9fa8?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
    }
    &.third {
      background-image: url("https://images.unsplash.com/photo-1426604966848-d7adac402bff?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
    }
    &.fourth {
      background-image: url("https://images.unsplash.com/photo-1552083375-1447ce886485?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
    }
    &.fifth {
      background-image: url("https://images.unsplash.com/photo-1565118531796-763e5082d113?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
    }
  }
}
